<template>
  <div id="app">
    <div id="mescroll" class="mescroll">
      <div>
        <Banner/>

        <!-- mark: 熱門榜單 -->
        <TopPannel>熱門榜單</TopPannel>
        <RankList />

        <!-- mark: 專家推薦 -->
        <TopPannel>專家推薦</TopPannel>
        <ExpertList :list="hotList"/>

        <!-- mark: 正在連紅 -->
        <TopPannel>正在連紅</TopPannel>
        <ExpertList :list="redList"/>

        <!-- mark: 熱門推薦 -->
        <TopPannel>熱門推薦</TopPannel>
        <RecommendList />
        <div class="bottom-fix"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from './components/Banner.vue'
  import Cell from './components/Cell.vue'
  import Section from './components/Section.vue'
  import ExpertList from './components/ExpertList.vue'
  import RecommendList from './components/RecommendList.vue'
  import RankList from './components/RankList.vue'
  import TopPannel from './components/TopPannel.vue'

  export default {
  // 1. 副作用
  name: 'app',
  // 5. 模板依赖
  components: {
    Banner,
    Cell,
    Section,
    ExpertList,
    RecommendList,
    RankList,
    TopPannel
  },
  // 8. 本地状态
  data: function() {
    return {
      mescroll: '',
      hotList:[],
      saleList:[],
      redList:[],
      swiper: {}
    }
  },
  // 9. 事件
  created: function() {
    this.hotexpert()
    this.redexpert()
    this.saleexpert()
  },
  // 10. 非响应式的属性
  methods: {
    hotexpert:function(){
      var that = this;
      this.$http.get(url+'queryhotexpert?limitStart=0&limitNum=48').then(function(response) {
        var res=response.data;
        if(res.code=="200"){
          var json=res.data
          if(json.length>0){
            that.hotList= json;
          }else{
            this.ishot = true
          }
        }
      }, function(response) {
        this.ishot=true
      })
    },
    redexpert:function(){
      var that = this;
      this.$http.get(url+'rec/reders?limitStart=0&limitNum=32').then(function(response) {
        var res=response.data;
        if(res.code=="200"){
          var json=res.data
          for (var i=0; i<json.length; ++i) {
            json[i].nickName = json[i].nickname;
            json[i].userid = json[i].userId;
            json[i].avatar = "https://mobile.gunqiu.com/avatar/" + json[i].userId + "?size=!100px"
          }

          if(json.length>0){
            that.redList= json;
          }else{
            this.isred=true
          }
        }
      }, function(response) {
        this.isred=true
      })
    },
    saleexpert:function(){
      var that = this;
      this.$http.get(url+'rec/hotsalers?limitStart=0&limitNum=48').then(function(response) {
        var res=response.data;
        if(res.code=="200"){
          var json=res.data
          if(json.length>0){
            that.saleList= json;
          }else{
            this.issale=true
          }
        }
      }, function(response) {
        this.issale=true
      })
    },
  }
}
</script>

