<template>
  <div class="playerCell" @click="opennative('userinfo',item.userid, item.nickName,'tjdtzjtx')">
  	<img :src="item.avatar" :onerror="'this.src=' + '\'https://i.pravatar.cc/150?u=' + item.avatar + '\''" class="avator">
	<div class="nickname">{{item.nickName}}</div>
  </div>
</template>

<script>

export default {
  props: {
    item: Object,
    
  },
  methods: {
    opennative: function(type,id,name,eventID){
      if(eventID!=""){
        gq.UMAnalytics(eventID)
      }
      var datas={
        n:type,
        v:{}
      }
      if(type=="userinfo") {
        datas.v.uid=id
        datas.v.nickname=name
        gq.open(datas)
      }else if(type=="tjinfo"){
        datas.v.id=id
        gq.open(datas)
      }else{
        gq.open(datas)
      }
    }
  }
}
</script>

<style scoped>
.playerCell {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.nickname {
  font-size: 15px;
  text-align: center;
}
.avator {
	width: 48px;
	height: 48px;
	margin-bottom: 20px;
	margin-top: 10px;
}
</style>
