<template>
  <div class="recommendList">
<!--     <Section /> -->
    <template v-for="(v,key) in list">
      <ExpertRecommend :newsId="v.newsId" :model="v" />
    </template>
    
  </div>
</template>

<script>
import ExpertRecommend from './ExpertRecommend.vue'
var url='https://mobile.gunqiu.com/interface/v3.6/';
export default {
  // 2. 全局感知
  name: 'RecommendList',
  // 5. 模板依赖
  components: {
    ExpertRecommend
  },
  // 7. 接口
  props: {

  },
  // 8. 本地状态
  data: function() {
      return {
        list:[],
        limitStart:0,
      }
  },
  // 9.
  created: function(){ 
    this.loadPage()
  },
  mounted: function() {
    var swiper = new Swiper('.swiper-container');    
  },
  
  // 10. 非响应式的属性
  methods: { 
      loadPage:function() {
        console.log("loadPage") 
        var url = "https://mobile.gunqiu.com/interface/v3.6/";
        this.$http.get(url+'rec/index?limitStart='+this.limitStart+'&limitNum=20').then((response) => {
          var res=response.data;
          if(res.code=="200"){ 
            if(res.data.length>0){
              if(res.data.length<20){ 
                this.more=false  
              }else{
                this.more=true 
              } 
            }else{
              this.isShow =true; 
              this.more=false  
            }  
            this.list=res.data
          }else{
            this.isShow =true; 
            this.more=false   
          } 
          this.loading=false  
          // this.$refs.wrapper.style.height=document.body.clientHeight+"px"
        }, (response) => {
          this.loading=false
          this.more=false   
        })
      },
      
    } 
}
</script>

