<template>
  <div class="swiper-container expert-list">
    <div class="swiper-wrapper">
      <template v-for="sublist in splitArray(8, list)">
        <div class="swiper-slide">
          <div class="expert-list">
            <template v-for="item in sublist">
              <ExpertCell :item="item" />
            </template>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import ExpertCell from './ExpertCell.vue'
var url='https://mobile.gunqiu.com/interface/v3.6/';
export default {
  // 2. 全局感知
  name: 'expert-list',
  // 5. 模板依赖
  components: {
    ExpertCell,
  },
  // 7. 接口
  props: {
    list: Array,
  },
  // 8. 本地状态
  data: function() {
    return {
      swiper: {}
    }
  },
  // 9. 事件
  created: function() {
    this.hotexpert()
  },
  mounted: function() {
    this.swiper = new Swiper('.expert-list', {
      grabCursor: true,
      observer:true,
      observeParents:true,
    });
  },
  updated: function() {
    for (var i=0; i<this.swiper.length(); ++i)
      this.swiper[i].update()
  },
  // 10. 非响应式的属性
  methods: {
    // hotlist
    splitArray:function(N,Q){
      var R = [],F;
      for (F = 0;F < Q.length;) R.push(Q.slice(F,F += N))
        return R
    },
  }
}
</script>

<style scoped>
.playerCell {
  width: 25%;
}

.expert-list {
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-bottom: 21px;
  width: 100%;
