<template>
  <div class="tj-banner">
    <div class="swiper-container" id="banner">
      <div class="swiper-wrapper">
        <template v-for="v in focus">
          <div class="swiper-slide">
            <img :src="v.pic" alt=""/>
          </div>
        </template>
      </div>
      <div class="swiper-pagination bannerPagination"></div>
    </div>
  </div>
</template>

<script>
  export default {
    // 2. 全局感知
    name: 'banner',
    // 8. 本地状态
    data: function() {
      return {
        focus: {},
        swiper: {}
      }
    },
    // 9. 事件
    created: function() {

    },
    mounted: function() {
      this.swiper = new Swiper('#banner', {
        // observer:true
        grabCursor: true,
        observer:true,
        observeParents:true,
      });
      this.$http.get('https://baoying-mobile.github.io/focuspicture.json')
      .then(res => {
        this.focus = res.data.data
      })
    },
    updated: function() {
      // this.swiper.update()
    },
  } 
</script>

<style scoped>
.swiper-container {
  width: 100%;
}
.swiper-slide {
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-slide img {
  width: 100%;
  height: auto;
}
</style>